

import{EmployeeService} from '../Employee.service';
import { TaskService } from '../Task.service';


import { Component , ViewContainerRef } from '@angular/core';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';


@Component({
  selector: 'app-task-assignment',
  templateUrl: './task-assignment.component.html',
  styleUrls: ['./task-assignment.component.css']
})
export class TaskAssignmentComponent  {

  users;
  Taskslist;
  randomdata;
  selectedcustomerId;
  
   AssignTask = { CustomerID: "", TaskIDs: [] };
   public showbtn = false
   public showloader = false;

  constructor(public _employeeservice: EmployeeService,public _taskservice: TaskService,public toastr: ToastsManager, 
    public _vcr: ViewContainerRef,) { 
      this.toastr.setRootViewContainerRef(_vcr);

  }


  getuserlist()
  {
    this._employeeservice.getuserlist().subscribe(
      x => {

         debugger;

          this.users = x
          debugger;
          console.log("VALUE RECEIVED: ", x);
         
        

      },
      x => {
          console.log("ERROR: ",x);
      },
      () => {

      
          console.log("Completed");
      }
    );
  }
 


  ngOnInit() {
    this.getuserlist()

  }
 
  getTasks(ID)
  {

    this.AssignTask.TaskIDs = []

    this.showloader = true;

    this.AssignTask.CustomerID = ID;

    this._employeeservice.getusertaskslist(ID).subscribe(
      x => {
        
          debugger;
          console.log("All Tasks ", x);
          this.Taskslist = x;
          console.log("Data: ", this.Taskslist);

          this.showbtn = true;

          var i = 0;
              
             
                for (i = 0; i < this.Taskslist.length; i++) {
                    debugger;
                    if (this.Taskslist[i].Checked == true) {
                        this.AssignTask.TaskIDs.push(this.Taskslist[i].Id);
                    }
                }

             console.log("Sent array");
             console.log(this.AssignTask);
      },
      x => {
          console.log("ERROR: ",x);
          this.showloader = false;
      },
      () => {

      
          console.log("Completed");

          this.showloader = false;

      }
    );
  }


  CheckedTask(ID)
  {

debugger;

    var index = this.AssignTask.TaskIDs.indexOf(ID);
    if (~index) {
      this.AssignTask.TaskIDs.splice(index, 1);

    } else {
      this.AssignTask.TaskIDs.push(ID);

    }
   


    console.log(this.AssignTask);
  }

  showSuccess(Message){
    this.toastr.success("Success", Message);
   }


  Assigntask()
  {
    this._employeeservice.saveAssignTask(this.AssignTask).subscribe(
      x => {


        this.showSuccess("Task Updated successfully");

        debugger;
        
          this.AssignTask.TaskIDs = []

          this.getTasks(this.AssignTask.CustomerID);

          console.log("All Tasks ", x);
         
      },
      x => {
         debugger;
          console.log("ERROR: ",x);
      },
      () => {

      
       
      }
    );
  }

  
}









