
import{EmployeeService} from '../Employee.service';

import { Component , ViewContainerRef } from '@angular/core';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';



@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.css']
})
export class EmployeeComponent {

  users;
  data;
  public edited =false;
  public showform = false;

  public dt:Date = new Date();


  public showgrid=true;

  public showloader =false;

  api;
  
  
  constructor(public _employeeservice: EmployeeService,public toastr: ToastsManager, 
    public _vcr: ViewContainerRef,) { 
      this.toastr.setRootViewContainerRef(_vcr);

  }

  



  getuserlist()
  {

    this.showloader =true;

    this._employeeservice.getuserlist().subscribe(
      x => {
          this.users = x
          this.data = x
          debugger;
          console.log("VALUE RECEIVED: ", x);
          console.log("Data: ", this.users);
         


  
      },
      x => {
          console.log("ERROR: ",x);
          this.showloader =false;
      },
      () => {

        this.showgrid = true;
          console.log("Completed");
          this.showloader =false;
      }
    );
  }


  

  ngOnInit() {

    this.getuserlist()
   
  }

  oldrecord;
  Newrecord = { Id:0, Name:"",Email:"", DOB:""}

  currentUserID = 0


  


  Addtolist(newdata)
  {

    newdata.Id = 0;

    debugger;
    this._employeeservice.adduser(newdata).subscribe(

      x => {
        debugger;
        this.showSuccess("Saved successfully")
        console.log("VALUE RECEIVED: ", x);
        this.getuserlist();

        this.showgrid = true;
     
    },

      x => {
debugger;

        console.log("ERROR: ",x);
         },
      ()=>{

        this.getuserlist()
      }
    )

   
  }

  deleteRecord(ID)
  {

    this._employeeservice.deleteCustomer(ID).subscribe(
      x => {
    
        this.showSuccess("Deleted successfully")
          console.log("Deleted record: ", x);
         
        
      },
      x => {
        debugger;
          console.log("ERROR: ",x);
      },
      () => {
          console.log("Completed");
          this.getuserlist()
      }
    );
   
  }

 

  UpdateRecord(obj)
  {

    debugger;

    this.showgrid = false;

    this.showform =true;


    this.edited = true;
    
    this.Newrecord = {Id:obj.ID, Name:obj.Name, Email:"Test@gmail.com", DOB:"11/11/2000"}

    this.currentUserID  = obj.ID;
    

  }

  saveupdatedrecord(Data)
  {
    debugger;

    this.Newrecord = {Id:this.currentUserID, Name:Data.Name, Email:Data.Email, DOB:Data.DOB}

    this._employeeservice.adduser(this.Newrecord).subscribe(

      x => {



       
        this.showSuccess("Updated successfully")
     
        console.log("VALUE RECEIVED: ", x);
        this.getuserlist()

     

        
     
    },

      x => {
debugger;

        console.log("ERROR: ",x);
         },
      ()=>{

        this.getuserlist()
      }
    )

    this.showform =false;
  }

  AddNew()
  {

   debugger;

    this.showgrid = false;

    this.edited = false;

    this.showform =true;

    this.Newrecord = { Id:0, Name:"",Email:"", DOB:""}
  }

  cancelform()
  {
    this.showgrid = true;
  }

  showSuccess(Message){
    this.toastr.success("Success", Message);
   }



  
}



