import { Component, OnInit } from '@angular/core';
import{TaskService} from '../Task.service';



@Component({
  selector: 'app-todos',
  templateUrl: './todos.component.html',
  styleUrls: ['./todos.component.css']
})
export class TodosComponent implements OnInit {

  tasks;
  public edited =false;
  public showform = false;

  public showloader =false;
  public showgrid=true;

  api;
  
  
  constructor(public _taskservice: TaskService) { 


  }





  gettasklist()
  {
    this.showloader =true;

    this._taskservice.gettasklist().subscribe(
      x => {
          this.tasks = x
          debugger;
          console.log("VALUE RECEIVED: ", x);
          console.log("Data: ", this.tasks);


  
      },
      x => {
          console.log("ERROR: ",x);
          this.showloader =false;
      },
      () => {

        this.showgrid = true;
          console.log("Completed");
          this.showloader =false;
      }
    );
  }


  

  ngOnInit() {

    this.gettasklist()
   
  }

  oldrecord;
  Newrecord = { Id:0, Name:"",Status:""}

  currentUserID = 0

  Addtolist(newdata)
  {

    newdata.Id = 0;

    debugger;
    this._taskservice.addtask(newdata).subscribe(

      x => {
        debugger;
        console.log("VALUE RECEIVED: ", x);
        this.Newrecord = { Id:0, Name:"",Status:""}

        this.showgrid = true;
     
    },

      x => {
debugger;

        console.log("ERROR: ",x);
         },
      ()=>{

        this.gettasklist()
      }
    )

   
  }

  deleteRecord(ID)
  {

    this._taskservice.deletetask(ID).subscribe(
      x => {
    
          debugger;
          console.log("Deleted record: ", x);
         
        
      },
      x => {
        debugger;
          console.log("ERROR: ",x);
      },
      () => {
          console.log("Completed");
          this.gettasklist()
      }
    );
   
  }

 

  UpdateRecord(obj)
  {

    debugger;

    this.showgrid = false;

    this.showform =true;

   

    this.edited = true;
    
    this.Newrecord = {Id:obj.Id, Name:obj.detail, Status:obj.status}

    this.currentUserID  = obj.Id;
    

  }

  saveupdatedrecord(Data)
  {
    debugger;

    this.Newrecord = {Id:this.currentUserID, Name:Data.Name, Status:Data.Status}

    this._taskservice.addtask(this.Newrecord).subscribe(

      x => {
        debugger;
        console.log("VALUE RECEIVED: ", x);
        this.gettasklist()
     
    },

      x => {
debugger;

        console.log("ERROR: ",x);
         },
      ()=>{

        this.gettasklist()
      }
    )

    this.showform =false;
  }

  AddNew()
  {
   this.Newrecord = { Id:0, Name:"",Status:""}
   this.edited = false;
    this.showform =true;
    this.showgrid = false;
  }


  

  cancelform()
  {
    this.showgrid = true;
  }


}



