import { Injectable } from '@angular/core';

import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import { Http, Response, RequestOptions, Headers } from "@angular/http";



@Injectable()
export class TaskService {

  constructor(public http: Http) {

    
    console.log("Service Is Initializes successfully");
    
   }
 
   tasklist:any[] = []

   gettasklist()
   {
     debugger;

    // this.userlist = JSON.parse(localStorage.getItem("allusers"))
    // return this.userlist;
     
    return this.http.get('http://poc.shivamitconsultancy.com/api/Task/')
    .map(res => res.json());

   }

   addtask(data)
  {
    debugger;

    let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded' });    
    //let body ="Id=0&detail=detail05&status=3";	
     let body = "Id=" + data.Id + "&detail=" + data.Name + "&status=" + data.Status;

    let url = 'http://poc.shivamitconsultancy.com/api/Task/'
    let options = new RequestOptions({ headers: headers});
    return this.http.post(url, body, options)
    .map((res: Response) => res.json());


   /* if(this.userlist==null)
    {
      this.userlist = [{Name:"Dashrath", Role:"Frontend developer", DOB:"09/06/1988",Email:"Test@test.com",gender:"Male"}]
    }
   
    this.userlist.push(data);
    localStorage.setItem("allusers",JSON.stringify(this.userlist))
    */
  }

  deletetask(ID) {

    debugger;
    return this.http.get('http://poc.shivamitconsultancy.com/api/Task/' + ID)
    .map(res => res.json());
  }



}



